@charset "UTF-8";
/* A Modern CSS Reset */
/* https://coliss.com/articles/build-websites/operation/css/css-reset-for-modern-browser.html */
*, *::before, *::after {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin: 0;
}

ul[role=list], ol[role=list], li {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

a {
  text-decoration: none;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, *::before, *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
}

.wrapper {
  display: flex;
}

.main {
  padding: 100px 80px 80px;
  background: #f9f9f9;
  width: calc(100% - 240px);
  margin-left: auto;
}

a {
  color: #3282c9;
}

.heading-lv1 {
  font-size: 40px;
  line-height: 1.5;
  color: #222;
}
@media screen and (max-width: 768px) {
  .heading-lv1 {
    font-size: 32px;
  }
}

.heading-lv2 {
  font-size: 36px;
  line-height: 1.5;
  text-align: center;
  color: #d40000;
  text-shadow: #fff264 2px 0px, #fff264 -2px 0px, #fff264 0px -2px, #fff264 0px 2px, #fff264 2px 2px, #fff264 -2px 2px, #fff264 2px -2px, #fff264 -2px -2px, #fff264 1px 2px, #fff264 -1px 2px, #fff264 1px -2px, #fff264 -1px -2px, #fff264 2px 1px, #fff264 -2px 1px, #fff264 2px -1px, #fff264 -2px -1px;
}
@media screen and (max-width: 768px) {
  .heading-lv2 {
    font-size: 28px;
  }
}

.font-mincho {
  font-family: "游明朝体", "Yu Mincho", YuMincho, serif;
}

.text {
  font-size: 18px;
  line-height: 1.5;
  margin-top: 32px;
}
.text:first-child {
  margin-top: 0;
}
.text + .text {
  margin-top: 16px;
}

.text-l {
  font-size: 30px;
}

.note {
  font-size: 14px;
  line-height: 1.5;
  margin-top: 16px;
  color: #777;
}

.textTime {
  margin-top: 24px;
  font-size: 16px;
  line-height: 1.4;
  color: #D46A00;
  text-align: right;
}

.header {
  background-color: #fff;
  border-bottom: solid 3px #d40000;
}

.nav {
  max-width: 1200px;
  margin: 0 auto;
}
.nav_list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.nav_item {
  display: block;
}
.nav_item + .nav_item {
  margin-left: 16px;
}
.nav_item a {
  font-size: 22px;
  font-weight: bold;
  color: #777;
  transition: all 0.3s;
  text-decoration: none;
}
.nav_item a:hover {
  text-decoration: underline;
}
.nav_item-first {
  margin-right: auto;
}

.aside {
  background-image: linear-gradient(to bottom, #ffffc6 0%, #ffdd74 100%);
  min-height: 100vh;
  width: 240px;
  max-width: 240px;
  z-index: 1;
}
.aside.is-fixed {
  position: fixed;
  top: 0;
}

.sidenav_item + * {
  margin-top: 2px;
}
.sidenav_item a {
  color: #3282c9;
  font-size: 20px;
  position: relative;
  width: 100%;
}

.card {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 32px;
}
.card__item {
  border-color: #fff;
  width: calc((100% - 24px) / 2);
  border: solid #d40000 4px;
  box-shadow: 8px 8px 0px 0px #d30000;
  transition: transform 0.3s;
}
.card__item:hover {
  transform: translateY(-20px);
}
.card__item:nth-of-type(n + 3) {
  margin-top: 40px;
}
.card__link {
  display: block;
  padding: 0 16px 16px 16px;
  text-decoration: none;
  transition: transform 0.3s;
}
.card__link:hover {
  transform: translateY(-5px);
}
.card__image {
  position: relative;
  text-align: center;
  margin: 0 auto;
}
.card__image img {
  margin: 0 auto;
  box-shadow: 1px 4px 10px #000;
  object-fit: cover;
}
.card__title {
  font-size: 28px;
  line-height: 1.4;
  font-weight: 700;
  color: #d40000;
  background-color: white;
  padding: 5px 16px;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.card__text {
  font-size: 22px;
  line-height: 1.4;
  font-weight: 500;
  color: #777;
  margin-top: 24px;
}

.section-guidance {
  margin-top: 80px;
  padding-top: 32px;
  border-top: double 1px #d40000;
}

.inner {
  background-color: #fff;
  border-radius: 4px;
  padding: 24px;
}

.intro {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.intro__character {
  width: 200px;
}
.intro__character__caption {
  color: #d40000;
  font-size: 14px;
  line-height: 1.6;
}
.intro__character__caption strong {
  text-align: center;
  display: block;
}
.intro__box {
  background: #fff;
  border: solid 4px #fcf264;
  text-align: center;
  padding: 14px 10px;
  flex: 1;
  margin-left: 80px;
  position: relative;
}
.intro__box::before {
  content: "";
  background: #fff;
  position: absolute;
  border: #fcf264 solid 3px;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  left: -3%;
  top: 13%;
}
.intro__box::after {
  content: "";
  background: #fff;
  position: absolute;
  border: #fcf264 solid 2px;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  left: -6%;
  top: 17%;
}

.postContent {
  margin-top: 32px;
  background: #fff;
  border-radius: 4px;
  padding: 24px 40px;
}
.postContent p + h2,
.postContent p + h3,
.postContent p + h4,
.postContent p + h5 {
  margin-top: 24px;
}
.postContent h2 + p {
  margin-top: 16px;
}
.postContent h2 {
  font-size: 24px;
  line-height: 1.4;
  background-color: #d40000;
  padding: 6px 10px;
  color: #fff;
  position: relative;
}
.postContent p {
  font-size: 18px;
  line-height: 1.8;
}

.articleCard {
  margin-top: 32px;
  width: 100%;
  column-count: 3;
  column-gap: 0;
}
.articleCard__item {
  margin: 16px;
  page-break-inside: avoid;
  break-inside: avoid;
  box-shadow: 8px 10px 10px -10px rgba(0, 0, 0, 0.8);
  display: inline-block;
  border: solid 4px #fff264;
  background-color: #fff;
  transition: all 0.3s;
}
.articleCard__item:hover {
  transform: translateY(-5px);
}
.articleCard__item a {
  padding: 16px;
  transition: all 0.3s;
  display: block;
}
.articleCard__item a img {
  max-height: 200px;
  width: auto;
  height: auto;
  margin: 0 auto;
}
.articleCard__title {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.4;
  margin-top: 16px;
  color: #d40000;
}
.articleCard__text {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.4;
  color: #777;
  margin-top: 16px;
}
.articleCard__time {
  display: block;
  font-size: 16px;
  line-height: 1.4;
  color: #D46A00;
  margin-top: 8px;
}

/*
Theme Name: blog
*/